body {
  font-family: "Poppins", "Trebuchet MS", sans-serif;
  margin: 0 auto;
}

.header {
  padding: 30px;
  background-color: #f9f9fc;
}
.header .header-menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .header-menu h3 {
  color: #5E3BEE;
}
.header ul {
  list-style: none;
  display: flex;
  gap: 50px;
}
.header ul a {
  text-decoration: none;
  color: black;
}
.header ul a a:visited {
  text-decoration: none;
  color: black;
}
.header .header-btn {
  color: white;
  background-color: #5E3BEE;
  border-radius: 50px;
  padding: 20px;
  border-style: none;
  width: 124px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

.img-section h3 {
  color: #5E3BEE;
  font-weight: 400;
}
.img-section h1 {
  margin: 0;
  font-size: xxx-large;
}
.img-section .main-img {
  display: flex;
  flex-direction: row;
  max-width: 100%;
  height: auto;
}

.content-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f9f9fc;
}

.text-content {
  flex-grow: 0;
  padding: 70px;
}
.text-content .flex {
  display: flex;
  margin-top: 50px;
}
.text-content .contact-btn {
  color: white;
  background-color: #5E3BEE;
  border-radius: 50px;
  border-style: none;
  width: 204px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: large;
}
.text-content .download-cv {
  background-color: transparent;
  border-style: none;
  width: 204px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: large;
}

.text-section {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
  margin: 75px;
}
.text-section h1 {
  margin: 0;
}
.text-section h2 {
  margin: 0;
  font-weight: 400;
}

.middle-text {
  text-align: center;
  margin: 60px;
}
.middle-text h3 {
  color: #5E3BEE;
  font-weight: 400;
}

.info-section {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}
.info-section .info-rectangle {
  width: 376px;
  height: 286px;
  border-radius: 24px;
  border: 1px solid;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-style: none;
  -webkit-box-shadow: 15px 26px 106px 18px rgba(145, 145, 145, 0.68);
  -moz-box-shadow: 15px 26px 106px 18px rgba(145, 145, 145, 0.68);
  box-shadow: 15px 26px 106px 18px rgba(145, 145, 145, 0.68);
}
.info-section .info-rectangle h3 {
  color: #5E3BEE;
  font-size: x-large;
}

.middle-text2 {
  background-color: #f9f9fc;
  text-align: left;
  padding: 70px;
}
.middle-text2 h1 {
  margin: 0;
  font-size: xx-large;
}
.middle-text2 h3 {
  color: #5E3BEE;
  font-weight: 400;
}
.middle-text2 .contact-btn {
  color: white;
  background-color: #5E3BEE;
  border-radius: 50px;
  border-style: none;
  width: 204px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-size: large;
}

.portfolio {
  padding: 70px;
}
.portfolio h3 {
  color: #5E3BEE;
  font-weight: 400;
}
.portfolio .head-lorem {
  text-align: right;
}
.portfolio .row {
  display: flex;
  flex-wrap: wrap;
}
.portfolio .col-5 {
  width: 50%;
}
.portfolio .flex {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}
.portfolio .card {
  width: 583px;
  height: 532px;
  border-radius: 24px;
  border: 1px solid;
  border-style: none;
  background-color: #C4C4C4;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  min-height: 200px;
  -webkit-box-shadow: -1px -1px 42px 0px rgba(0, 0, 0, 0.33);
  -moz-box-shadow: -1px -1px 42px 0px rgba(0, 0, 0, 0.33);
  box-shadow: -1px -1px 42px 0px rgba(0, 0, 0, 0.33);
}
.portfolio .card h3 {
  font-weight: bold;
  color: black;
}
.portfolio .card .card-text {
  background-color: white;
  margin-top: auto;
  border-radius: 0px 0px 24px 24px;
  border-bottom: 1px solid black;
  border-style: none;
  text-align: left;
  padding-left: 40px;
}

.testimonials .flex {
  display: flex;
  justify-content: center;
  margin: 20px;
}
.testimonials .review-rectangle {
  width: 584px;
  height: 369px;
  border-radius: 24px;
  border: 1px solid black;
  margin: 20px;
  padding: 20px;
  -webkit-box-shadow: 0px -1px 81px -34px rgba(0, 0, 0, 0.75);
  -moz-box-shadow: 0px -1px 81px -34px rgba(0, 0, 0, 0.75);
  box-shadow: 0px -1px 81px -34px rgba(0, 0, 0, 0.75);
  border-style: none;
}
.testimonials .review-rectangle .stars {
  color: #EEC01C;
  font-size: xx-large;
  margin-bottom: 13px;
}
.testimonials .review-rectangle .subtext {
  font-size: x-large;
}
.testimonials .review-rectangle .user-circle {
  background-color: #5E3BEE;
  border-radius: 50%;
  width: 64px;
  height: 64px;
  flex-shrink: 0;
}
.testimonials .review-rectangle .comment {
  color: #5E3BEE;
  font-size: xx-large;
  margin-left: auto;
}
.testimonials .review-rectangle h3 {
  margin: 0;
}
.testimonials .review-rectangle p {
  margin: 0;
}
.testimonials .profile-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: 80px;
}
.testimonials .user-info {
  margin-left: 20px;
  padding: 0;
}

.banner {
  background-image: url("../Img/cta-sectionimg.png");
  height: 471px;
  text-align: center;
  align-content: center;
  background-position: center center;
  background-repeat: no-repeat;
}
.banner .contact-btn {
  color: white;
  background-color: #5E3BEE;
  border-radius: 50px;
  border-style: none;
  width: 204px;
  height: 70px;
  cursor: pointer;
  font-size: large;
}

.footer {
  margin-top: 100px;
  border-top: 1px solid black;
  border-radius: 24px 24px 0px 0px;
  border-style: none;
  background-color: #404040;
  color: white;
}
.footer .footer-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.footer .text-container {
  padding: 50px;
}
.footer .contacts-container {
  margin: 80px;
}
.footer .socials {
  display: flex;
  gap: 20px;
}
.footer .flex {
  display: flex;
  flex-direction: row;
}
.footer .header-menu {
  display: flex;
  justify-content: space-between;
  padding: 30px;
}
.footer .header-menu ul {
  list-style: none;
  justify-content: space-around;
  display: flex;
  gap: 50px;
  padding: 0;
}
.footer .header-menu ul a {
  text-decoration: none;
  color: white;
}
.footer .header-menu ul a a:visited {
  text-decoration: none;
  color: white;
}
.footer .header-menu ul p {
  text-align: center;
}